import request from '@/utils/request'; // import localRequest from '@/utils/localRequest'

export function ranking(data) {
  return request({
    url: '/app/business/goodslist/',
    method: 'post',
    data: data
  });
}
export function goodstendency(data) {
  return request({
    url: '/app/business/goodstendency',
    method: 'post',
    data: data
  });
}